@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
  background-color: #000630;
  color:white;
}

.logo-web{
  width: 40%;
  height: auto;
  max-width: 450px;
  min-width: 160px;
  max-height: 200px;
  align-self: center;
  padding-top: 0px;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 6px;
  outline: none;
  border: 2px solid white;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}
.pe-2{
  font-size: 13px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
  height: auto;
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  font-weight:bolder;
}
.eventList-container{
  max-width: 1500px;
}
.event {
  padding: 15px 15px 15px;
  margin-bottom: 10px;
  text-align: left;
  border: 2px solid #323759;
  border-radius: 6px;
  position: relative;
  background-color: #323759;
}

#event-card{
  padding: 5px;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 3px;
  margin: 5px;
  /* border-radius: 4px; */
  outline: none;
  cursor: pointer;
}

#link:hover, #card-title, a:hover{
  color: #44ddd1;
}
#card-title{
  font-size: 22px;
}

#cityImg{
  padding: 3px;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 150px;
  top: 30px;
  text-align: center;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert {
  font-size: 13px;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

.login-button {
color: #494949;
text-transform: uppercase;
text-decoration: none;
background: #ffffff;
border: 4px solid #494949;
display: inline-block;
transition: all 0.4s ease 0s;
min-width: 10rem;
margin: 1rem;
}
.google-btn {
width: 184px;
height: 42px;
margin: 1rem;
background-color: #4285f4;
border-radius: 2px;
box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
background: #1669f2;
}
.google-icon-wrapper {
position: absolute;
margin-top: 1px;
margin-left: 1px;
width: 40px;
height: 40px;
border-radius: 2px;
background-color: #fff;
}
.google-icon {
position: absolute;
margin-top: 10px;
margin-left: -7px;
width: 18px;
height: 18px;
}
.btn-text {
float: right;
color: #fff;
font-size: 14px;
letter-spacing: 0.2px;
font-family: "Roboto";
background-color: #4285f4;
width: 80%;
height: 100%;
text-align: center;
}



@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}